@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
/* Fonts */

@font-face {
    font-family: Custom;
    src: url(/ingresse-front-end-challenge/static/media/custom.7eb50ea6.eot);
    src:
        url(/ingresse-front-end-challenge/static/media/custom.7eb50ea6.eot?#iefix) format("embedded-opentype"),
        url(/ingresse-front-end-challenge/static/media/custom.cd0b2397.woff2) format("woff2"),
        url(/ingresse-front-end-challenge/static/media/custom.43c5c41b.woff) format("woff"),
        url(/ingresse-front-end-challenge/static/media/custom.a377c50c.ttf) format("truetype"),
        url(/ingresse-front-end-challenge/static/media/custom.bb655170.svg#ITC-ITCAvantGardeW1G-Bk) format("svg");
}

@font-face {
    font-family: CustomBold;
    src: url(/ingresse-front-end-challenge/static/media/custom-bold.10319942.eot);
    src:
        url(/ingresse-front-end-challenge/static/media/custom-bold.10319942.eot?#iefix) format("embedded-opentype"),
        url(/ingresse-front-end-challenge/static/media/custom-bold.e665cc91.woff2) format("woff2"),
        url(/ingresse-front-end-challenge/static/media/custom-bold.0b628ce3.woff) format("woff"),
        url(/ingresse-front-end-challenge/static/media/custom-bold.255fb355.ttf) format("truetype"),
        url(/ingresse-front-end-challenge/static/media/custom-bold.77b6354e.svg#custom-bold) format("svg");
}

/* Base Styles */
* {
    box-sizing: border-box;
}

html,
body,
#root {
    font-family   : "Custom", "Roboto", "Helvetica Neue", "Arial", sans-serif;
    font-weight   : 400;
    font-size     : 16px;
    line-height   : 20px;
    letter-spacing: 0.5px;

    margin : 0;
    padding: 0;

    display: -webkit-flex;

    display: flex;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    -webkit-flex-direction: column;
            flex-direction: column;

    width     : 100%;
    height    : auto;
    min-height: 100%;

    color           : white;
    background-color: #101a4c;
}

::-moz-selection {
    color     : white;
    background: #723c85;
}

::selection {
    color     : white;
    background: #723c85;
}

a,
button,
input,
select,
textarea {
    font-family: "Custom", "Roboto", "Helvetica Neue", "Arial", sans-serif;
    -webkit-tap-highlight-color: rgba(114, 60, 133, 0.25);
}

/* Generic Styles */
.text-bold {
    font-weight: 500;
}

.text-uppercase {
    text-transform: uppercase;
}

.text-lead {
    font-size: 20px;
}

/* Routes Transitions */
.fade-exit {
    opacity   : 0;
    -webkit-transition: opacity 250ms ease-out;
    transition: opacity 250ms ease-out;
}

.fade-enter {
    opacity: 0;
    z-index: 1;
}

.fade-enter.fade-enter-active {
    opacity   : 1;
    -webkit-transition: opacity 250ms ease-in;
    transition: opacity 250ms ease-in;
}

